<template>
  <div>
    <el-button
      type="primary"
      plain
      icon="el-icon-plus"
      size="mini"
      @click="addRules"
      >新增场景</el-button
    >
    <el-table class="mt-10" :data="sceneList" style="width: 100%">
      <el-table-column label="序号">
        <template slot-scope="scope">{{ scope.$index + 1 }}</template>
      </el-table-column>
      <el-table-column prop="scene_name" label="场景名称"> </el-table-column>
      <el-table-column prop="scene_remark" label="场景备注"> </el-table-column>
      <el-table-column label="操作">
        <template slot-scope="scope">
          <el-button type="text" @click="association(scope.row)">
            <i title="算法关联" class="icon iconf-configuration"></i>
          </el-button>
          <el-button
            type="text"
            icon="el-icon-edit"
            @click="editData(scope.row)"
          />
          <el-button type="text" @click="delData(scope.row)">
            <i title="删除" class="el-icon-delete" style="color: red"></i>
          </el-button>
        </template>
      </el-table-column>
    </el-table>
    <el-dialog
      :title="title"
      :visible.sync="addVisible"
      width="30%"
      :close-on-click-modal="false"
    >
      <el-form
        :model="addForm"
        ref="addForm"
        label-width="120px"
        class="mt-20"
        size="small"
      >
        <el-form-item label="场景名称:" prop="scene_name">
          <el-input v-model="addForm.scene_name"></el-input>
        </el-form-item>
        <el-form-item label="场景备注:" prop="scene_remark">
          <el-input v-model="addForm.scene_remark"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="addVisible = false">取 消</el-button>
        <el-button type="primary" @click="submitForm('addForm')"
          >确 定</el-button
        >
      </span>
    </el-dialog>
    <el-dialog
      title="算法配置"
      :visible.sync="algorithmVisible"
      width="60%"
      :close-on-click-modal="false"
    >
      <el-table
        :data="algorithmList"
        style="width: 100%"
        @select="chooseAlgorithm"
        ref="algorithmTable"
      >
        <el-table-column type="selection" width="55"> </el-table-column>
        <el-table-column type="index" label="序号" align="center">
        </el-table-column>
        <el-table-column prop="ai_code" label="报警编号" align="center">
        </el-table-column>
        <el-table-column prop="ai_name" label="算法名称" align="center">
        </el-table-column>
      </el-table>
      <pagination
        class="float-right mt-10"
        v-show="total > 0"
        :total="total"
        :page.sync="params.page"
        :limit.sync="params.size"
        @pagination="getAlgorithmList"
        layout="total, prev, pager, next"
        small
      />
    </el-dialog>
  </div>
</template>

<script>
export default {
  data() {
    return {
      sceneList: [],
      addForm: {},
      addVisible: false,
      title: "新增场景",
      addRulesObject: {},
      updetaData: {},
      algorithmVisible: false,
      algorithmList: [],
      params: {
        page: 1,
        size: 10,
      },
      total: 0,
      sceneItem: {},
    };
  },
  created() {
    this.getSceneList();
    this.getAlgorithmList();
  },
  methods: {
    getDataAiSceneList() {
      if (this.$refs.algorithmTable) {
        this.$refs.algorithmTable.clearSelection();
      }
      this.$api
        .get("/ai/getDataAiSceneList", {
          params: {
            scene_id: this.sceneItem.scene_id,
          },
        })
        .then((res) => {
          if (res.data.data) {
            let data = res.data.data;
            this.$nextTick(() => {
              data.forEach((item) => {
                const target = this.algorithmList.find(
                  (row) => row.ai_id === item.ai_id
                );
                target &&
                  this.$refs.algorithmTable.toggleRowSelection(target, true);
              });
            });
          }
        });
    },
    chooseAlgorithm(val, data) {
      const url =
        val.indexOf(data) > -1 ? "/ai/addDataAiScene" : "/ai/delDataAiScene";
      this.$api
        .post(url, {
          ai_id: data.ai_id,
          scene_id: this.sceneItem.scene_id,
        })
        .then(() => {
          this.$message.success("操作成功");
        });
    },
    getAlgorithmList() {
      this.$api
        .get(`/ai/getDataAiList`, { params: this.params })
        .then((res) => {
          if (res.data.data) {
            this.algorithmList = res.data.data;
            this.total = res.data.total;
          }
        });
    },
    association(row) {
      this.sceneItem = row;
      this.algorithmVisible = true;
      this.getDataAiSceneList();
    },
    submitForm() {
      const isEditMode = !!this.addForm.scene_id;
      const apiUrl = isEditMode ? "/ai/updateDataScene" : "/ai/addDataScene";
      this.$api
        .post(apiUrl, this.addForm)
        .then(() => {
          this.addVisible = false;
          this.$message.success("操作成功");
          this.getSceneList();
        })
        .catch((error) => {
          console.error("操作失败:", error);
          this.$message.error("操作失败");
        });
    },
    delData(row) {
      this.$confirm("是否删除该数据?", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning",
      })
        .then(() => {
          this.$api
            .post("/ai/delDataAiScene", {
              scene_id: row.scene_id,
            })
            .then(() => {
              this.$message.success("操作成功");
              this.getSceneList();
            });
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: "已取消删除",
          });
        });
    },
    editData(row) {
      this.title = "编辑场景";
      this.addVisible = true;
      this.addForm = { ...row };
    },
    addRules(row) {
      this.addForm = {};
      this.title = "新增场景";
      this.addVisible = true;
      this.addRulesObject = row;
    },
    getSceneList() {
      this.$api
        .get("/ai/getDataSceneList", {
          params: {
            page: 1,
            size: 3,
          },
        })
        .then((res) => {
          this.sceneList = res.data.data;
        });
    },
  },
};
</script>

<style lang="scss" scoped></style>
